.sr-only,
.sr-only a {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  font-size: 1px;
  line-height: 1;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
  outline: none;
  visibility: hidden;
}


